export default function FormValidator(values, check){
    let errors = {};

    if(!values.username.trim()){
        errors.username = "Pole nie moe by puste"
    }

    if(!values.email.trim()){
        errors.email = "Pole nie moe by puste"
    }
    else if(!/\S+@\S+\.\S+/.test(values.email)){
        errors.email = "Nieprawidowy adres e-mail"
    }

    if(!values.password){
        errors.password = "Pole nie moe by puste"
    }
    else if (values.password.length < 8){
        errors.password = "Sabe haso"
    }

    if(!values.passwordRepeat){
        errors.passwordRepeat = "Pole nie moe by puste"
    }
    else if (values.passwordRepeat !== values.password) {
        errors.passwordRepeat = 'Hasa nie zgadzaj si';
    }

    if(!check){
        errors.rules = "Pole musi by zaznaczone"
    }

    return errors;
}